import React from 'react';
import { NavLink } from 'react-router-dom';
import logo2 from '../assets/img/logos/logo.png';

const Navbar = React.memo(() => {
  return (
    <div>
      <header className="header-style4 ">
        <div className="navbar-default">
          <div className="container">
            <div className="row align-items-center">
              <div className="col-12">
                <div className="navbar-header navbar-header-custom row">
                  <div className="col-xl-2 col-lg-3">
                    {/* Start logo */}
                    <NavLink to="/" className="navbar-brand logodefault">
                      <img id="logo" src={logo2} alt="logo" />
                    </NavLink>
                    {/* End logo */}
                  </div>
                  <div className="col-lg-9 offset-xl-1 offset-lg-0 d-none d-lg-block">
                    <div className="row">
                      <div className="col-md-4">
                        <div className="w-100">
                          <span className="display-inline-block w-40px align-top">
                            <i className="far fa-clock text-primary display-20" />
                          </span>
                          <p className="d-inline-block m-0 w-75 align-top ps-2">
                            <span className="font-weight-600 d-block">
                              Mon - Fri 08:00 AM-9:00 PM
                            </span>
                            <span className="font-weight-600">
                              Sat - Sun: Closed
                            </span>
                          </p>
                        </div>
                      </div>
                      <div className="col-md-4">
                        <div className="w-100">
                          <span className="d-inline-block w-40px align-top">
                            <i className="fas fa-mobile-alt text-primary display-20" />
                          </span>
                          <p className="d-inline-block m-0 w-75 align-top">
                            <span className="font-weight-600 d-block">
                              +91 7776827177 <br />
                              +91 9172530151
                            </span>
                            <span className="font-weight-600">
                              info@shekruweb.com
                            </span>
                            <br />
                          </p>
                        </div>
                      </div>
                      <div className="col-md-4">
                        <div className="w-100">
                          <span className="d-inline-block w-40px align-top">
                            <i className="fas fa-map-marker-alt text-primary display-20" />
                          </span>
                          <p className="d-inline-block m-0 w-75 align-top">
                            <span className="font-weight-600 d-block">
                              Office no 6, 2nd Floor Manogat Appt, Swarget, Pune, Maharashtra-411037
                            </span>
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="menu_area alt-font">
            <div className="container">
              <nav className="navbar navbar-expand-lg navbar-light p-0 sticky-top">
                <div className="navbar-toggler" />
                {/* Menu area */}
                <ul className="navbar-nav " id="nav" >
                  <li className=''>
                    <NavLink
                    
                      to="/"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Home
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/about"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      About
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/services"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Services
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/products"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Products
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/success-stories"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Success Stories
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/partners"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Partners
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/technologies"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Technologies
                    </NavLink>
                  </li>
                  <li className=''>
                    <span className="" />
                    <NavLink
                      to="/industries"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Industries
                    </NavLink>
                    <ul className="">
                      <li>
                        <NavLink
                          to="/transportation"
                          className={({ isActive }) => (isActive ? "text-primary" : "")}
                        >
                          Transportation
                        </NavLink>
                      </li>
                      <li >
                        <NavLink
                          to="/eCommerce"
                          className={({ isActive }) => (isActive ? "text-primary" : "")}
                        >
                          E-Commerce
                        </NavLink>
                      </li>
                      <li>
                        <NavLink
                          to="/stock-exchange"
                          className={({ isActive }) => (isActive ? "text-primary" : "")}
                        >
                          Stock Exchange
                        </NavLink>
                      </li>
                    </ul>
                  </li>
                  <li>
                    <NavLink
                      to="/careers"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Careers
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/clients"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Clients
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/contact"
                      className={({ isActive }) => (isActive ? "text-primary" : "")}
                    >
                      Contact
                    </NavLink>
                  </li>
                </ul>
                {/* End menu area */}
              </nav>
            </div>
          </div>
        </div>
      </header>
    </div>
  );
});

export default Navbar;
